import React, { useEffect, useRef } from 'react';

const PoseDetection = ({ exerciseType }) => {
  const imgRef = useRef(null);

  useEffect(() => {
    if (imgRef.current && exerciseType === 'squats') {
      imgRef.current.src = `${process.env.REACT_APP_API_URL}/api/exercise/squats/stream`;
    }
  }, [exerciseType]);

  return (
    <div className="pose-detection">
      <img 
        ref={imgRef} 
        alt="Pose Detection Stream" 
        style={{ 
          width: '100%', 
          height: '100%',
          objectFit: 'contain',
          backgroundColor: '#000'
        }}
      />
    </div>
  );
};

export default PoseDetection; 